.paymentForm {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.formContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.paymentForm label {
  margin-top: 20px;
}

.paymentForm input {
  padding: 7px;
}

.formElem {
  display: grid;
  grid-template-columns: repeat(2, 18em);
  gap: 0 2em;
}

.btnContainier {
  width: 100%;
  display: flex;
  justify-content: center;
}

.button {
  background-color: var(--not-member);
  padding: 10px 30px;
  border-radius: 10px;
  margin-top: 30px;
}

.header {
  text-align: center;
}
